.navbar {
    width: 350px;
    height: 100vh;
    background-color: rgb(218, 235, 205);
    display: flex;
    flex-direction: column;
    align-items: center;
}
.navbar_logo {
    height: 36px;
    width: auto;
}



.navbar > ul > li > a:hover {
    color:palegreen;
}

.navbar .active {
    font-weight: 700;
    color: red;
}
